export const showCounts = (counts: Obj, nicNema = '⭕', neZnojdzeno = '⭕') => {
  if (!counts) {
    return '…';
  }
  const { total, actual } = counts;
  if (!total) {
    return nicNema;
  }
  if (!actual) {
    return neZnojdzeno;
  }
  if (actual === total) {
    return total;
  }
  return actual + ' / ' + total;
};
